@Composable
fun Row(content: @Composable () -> Unit) {
	Div(attrs = {
		classes("row")
	}) {
		content()
	}
}

class ScannerState(elementId: String) {
	private val scanner: Html5Qrcode by lazy { Html5Qrcode(elementId) }
	fun start(ok: (r: String) -> Unit, error: (r: String) -> Unit) {
		scanner.start(
			jso { facingMode = "environment" },
			js("{}"),
			ok,
			error
		)
	}
	fun stop() {
		scanner.stop()
	}
	fun pause() {
		scanner.pause()
	}
}

@Composable
fun Card(t: String) {
	Div(
		attrs = {
			classes("card")
		}
	) {
		Text(t)
	}
}

@Composable
fun rememberScannerState(elementId: String) = remember { ScannerState(elementId) }

